import { _decorator } from 'cc';

export class QuizData {

    public pathToImage: string;
    public description: string;
    public wordVariants: string[];
    public correctVariantIndex: number;

    build(level: string) {
        let splits = level.split('&');

        this.pathToImage = splits[0];
        this.description = splits[1];
        this.wordVariants = splits[2].split(',');
        this.correctVariantIndex = Number(splits[3]);
    }
}
